<template>
  <div>
    <van-datetime-picker v-model="currentDate" item-height="44" @change="dateChnage" type="datetime" visible-item-count="5"
      title="修改北京时间" />
  <!--  <div style="display: flex;justify-content: center;align-items: center;margin: 5px 0;">
      <van-switch v-model="checked" @change="dateChnage" size="24px" />
      <div style="margin-left: 10px;">是否启用夏令时（只调整美国时区）</div>
    </div> -->
    <div class="timeArea">
      <div>东京<div class="time" v-html="h"></div>
      </div>
      <div>伦敦<div class="time" v-html="a"></div>
      </div>
      <div>迪拜<div class="time" v-html="b"></div>
      </div>
      <div>伊斯坦布尔<div class="time" v-html="c"></div>
      </div>
      <div>卡拉奇<div class="time" v-html="d"></div>
      </div>
      <div>班加罗尔<div class="time" v-html="e"></div>
      </div>
      <div>胡志明市<div class="time" v-html="f"></div>
      </div>
      <div>新加坡<div class="time" v-html="g"></div>
      </div>
      <div>洛杉矶<div class="time" v-html="i"></div>
      </div>
      <div>芝加哥<div class="time" v-html="j"></div>
      </div>
      <div>纽约<div class="time" v-html="k"></div>
      </div>
      <div>巴塞罗那<br />法兰克福<div class="time" v-html="l"></div>
      </div>
      <div>奥斯陆<br />阿姆斯特朗<div class="time" v-html="l"></div>
      </div>
    </div>
  </div>
</template>

<script>
  export default {
    data() {
      return {
        currentDate: new Date(),
        checked: false,
        a: '',
        b: '',
        c: '',
        d: '',
        e: '',
        f: '',
        g: '',
        h: '',
        i: '',
        j: '',
        k: '',
        l: ''
      }
    },
    mounted: function() {
      this.dateChnage();
    },
    methods: {
      dateChnage() {
        this.a = this.getLocalTime(1);
        this.b = this.getLocalTime(4);
        this.c = this.getLocalTime(3);
        this.d = this.getLocalTime(5);
        this.e = this.getLocalTime(5.30);
        this.f = this.getLocalTime(7);
        this.g = this.getLocalTime(8);
        this.h = this.getLocalTime(9);

          this.i = this.getLocalTime(-8);
          this.j = this.getLocalTime(-4);
          this.k = this.getLocalTime(-3);
        // if (this.checked) {

        //   this.i = this.getLocalTime(-8);
        //   this.j = this.getLocalTime(-4);
        //   this.k = this.getLocalTime(-3);
        // } else {
        //   this.i = this.getLocalTime(-7);
        //   this.j = this.getLocalTime(-5);
        //   this.k = this.getLocalTime(-4);
        // }
        this.l = this.getLocalTime(2);
      },
      getLocalTime(i) {
        //参数i为时区值数字，比如北京为东八区则输进8,西5输入-5
        if (typeof i !== 'number') return;
        var d = this.currentDate;
        //得到1970年一月一日到现在的秒数
        var len = d.getTime();
        //本地时间与GMT时间的时间偏移差
        var offset = d.getTimezoneOffset() * 60000;
        //得到现在的格林尼治时间
        var utcTime = len + offset;
        var time = new Date(utcTime + 3600000 * i);
        return `${time.getMonth()+1}月${time.getDate()}日<span class="hour">${time.getHours()}时${time.getMinutes()}分</span>星期${time.getDay()}`
      }
    }
  }
</script>

<style scoped>
  >>>.van-picker__cancel,
  >>>.van-picker__confirm {
    display: none;
  }

  >>>.van-picker__toolbar {
    justify-content: center;
  }

  .timeArea {
    width: 100%;
    border-top: 1px solid #23ade5;
  }

  .timeArea>div {
    margin: auto 20px;
    display: flex;
    justify-content: space-between;
    align-items: center;
    border-bottom: 1px solid #23ade5;
    padding: 5px 0;
  }

  >>>.hour {
    color: #23ade5;
    margin: 0 10px;
  }
</style>



